*{
  box-sizing: border-box;
}

.App{
  width: 100%;
  min-height: 100vh;
  background-color: beige;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  overflow: auto; 
}

.cvEditor{
  width: 600px;
  background-color: aquamarine;
  padding: 2rem;
  border: 1px solid black;
  margin: 1rem;
  @media (max-width: 1300px) {
   margin: .5rem;
  }
}

.cvPage{
  width: 600px;
  background-color: rgb(227, 255, 227);
  padding: 2rem;
  border: 1px solid black;
  margin: 1rem;
  @media (max-width: 1300px) {
    margin: .5rem;

  }
}

/* //---------PROFILE--------------- */
.profile{
  display: flex;
  flex-direction: column;
}

.profile h1{
  margin-bottom: 0;
}

.profile h3{
  margin-bottom: .5rem;
  margin-top: .5rem;
}


.profileRow1{
  display: flex;
  border-bottom: 1px solid blue;
}

.profileRow1Col2{
  margin-left: auto;
  margin-bottom: 0;
  text-align: right; 
}

.profileRow2 .profileSummary{
  text-align: justify;
  text-indent: 25px;
}


.profile img{
  height: 1.3rem;
  background-color: transparent;
}

/* -----------SKILLS */
.skills{
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.skills h2{
  border-bottom: 1px solid blue;
}

/* -----------WORK EXPERIENCE */

.workExperienceTitle{
  border-bottom: 1px solid blue;
}

.workExperienceRow1 {
  display:flex ;
  margin-bottom: .25rem;
}

.workExperienceRow1 > * {
  display: inline;
}

.workExperienceRow1 .company{
  margin-left: auto;
  border-right: 1px solid black;
  margin-right: .25rem;
  padding-right: .25rem;
}

.workExperienceRow1 .endDate{
  margin-left: .25rem;
}

.workExperienceRow1 .position, .company, .endDate, .startDate{
  font-weight: bold;
}


.workExperienceRow2{
  margin-bottom: 1rem;
  text-align: justify;
  text-indent: 25px;
  white-space: pre-wrap;
}

/* ---------------EDUCATION */

.educationTitle{
  border-bottom: 1px solid blue;
}

.educationRow1 {
  display:flex ;
  margin-bottom: .25rem;
}

.educationRow1  > * {
  display: inline;
}

.educationRow1 .degree{
  font-weight: bold;
}

.educationRow1 .graduationYear{
  margin-left: auto;
  margin-right: .25rem;
  padding-right: .25rem;
  font-weight: bold;
}

.educationRow2{
  margin-bottom: 1rem;
}



/* ---------------CV EDITOR */

/* ---------------InputBox */

.inputBox{
  
  flex-direction: column;
  width: 100%;
}

.input{
  display: flex;
  justify-content: space-between;
}


.workIndex{
  margin-bottom: 0;
  margin-top: 1rem;
}

.cvEditor h1{
  margin-top: 0;
  text-align: center;

}


.skillsEditor input{
  margin-left: auto;
}

.deleteButton, .addButton{
  display: flex;
}

.deleteButton button, .addButton button{
  margin-left: auto;
}

.deleteButton button{
background-color: crimson;
color: white;
}

.addButton button{
  margin-top: .5rem;
  background-color: green;
  color: white;
}

.input button{
  background-color: crimson;
  color: white; 
}

.collapseBtn{
  margin-left: auto;
  width: 2rem;
  transform: rotate(-180deg);
  transition: transform 0.3s;
}

.miniHeader{
  margin-top: 1rem;
  margin-bottom: .5rem;
  display: flex;
  border-bottom: 1px solid blue;
}

.rotate {
  transform: rotate(-90deg);
  transition: transform 0.3s;
}







